
import React,{Fragment} from 'react';
import '../../style/main.scss'
import {Component} from 'react';
import {NavLink} from 'react-router-dom';
import logo from '../../img/logo.png'

const List = (props) =>{
    return(
        <li className="nav_menu_list">
            <NavLink to={props.link} className="nav_menu_list_link" activeClassName="active"> {props.name} </NavLink>
        </li>
    )
}

export default class Navbar extends Component {
    render(){
        return(
            <Fragment>
                <nav>
                    <ul className="nav_menu">
                        <List link="/me" name="Iam"/>
                        <List link="/work" name="Work"/>
                    </ul>
                    <div className="nav_logo">
                        <NavLink to="/" exact className="logo" activeClassName="aktif">
                            <img src={logo} alt="logo" />
                        </NavLink>
                    </div>
                    <ul className="nav_menu">
                        <List link="/blog" name="Blog"/>
                        <List link="/contact" name="Ask"/>
                    </ul>
                </nav>
            </Fragment>
    )
}}

